<template>
  <div>
    <h3>{{ id ? "编辑" : "新建" }}生活</h3>
    <el-form
      label-width="120px"
      @submit.native.prevent="save"
      style="width:45rem;"
    >
      <el-form-item label="照片">
        <el-upload
          class="avatar-uploader"
          :action="uploadUrl"
          :show-file-list="false"
          :on-success="showAvatar"
          :headers="mixGetAuthHeaders()"
        >
          <img v-if="life.image" :src="life.image" class="avatar" />
          <i v-else class="el-icon-plus avatar-uploader-icon"></i>
        </el-upload>
      </el-form-item>
      <el-form-item label="题目（可选）">
        <el-input v-model="life.description"></el-input>
      </el-form-item>
      <el-form-item label="想说的">
        <el-input
          v-model="life.content"
          type="textarea"
          :autosize="{ minRows: 4, maxRows: 6 }"
        ></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" native-type="submit">保存</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
  props: {
    id: {}
  },
  data() {
    return {
      life: {
        image: ""
      }
    };
  },
  methods: {
    async save() {
      if (this.id) {
        await this.$http.put(`/life/${this.id}`, this.life);
      } else {
        await this.$http.post("/life", this.life);
      }
      this.$router.push("/life/list");
      this.$message({
        type: "success",
        message: "保存成功"
      });
    },
    showAvatar(val) {
      this.life.image = val.url;
    },
    async fetch() {
      const res = await this.$http.get(`/life/${this.id}`);
      this.life = res.data;
      this.life = Object.assign({}, this.life, res.data); //把 this.life{}中，再把res.data添加到{}中，如果有覆盖，没有保存
    }
  },
  created() {
    this.id && this.fetch();
  }
};
</script>

<style>
.avatar-uploader .el-upload {
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}
.avatar-uploader .el-upload:hover {
  border-color: #409eff;
}
.avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 8rem;
  height: 8rem;
  line-height: 8rem;
  text-align: center;
}
.avatar {
  width: 8rem;
  height: 8rem;
  display: block;
}
</style>
